@import '../../global.scss';

.contact {
  background-color: $darkNavy;
  color: #FFF;
  height: 100%;
  position: relative;

  .contact-bg-line {
    background-color: $lightBlue;
    height: 100%;
    position: absolute;
    width: 20px;
  }

  .wrapper {
    display: flex;
    padding: 50px;

    @include mobile {
      flex-direction: column;
    }

    .left-side {
      display: flex;
      flex-direction: column;
      flex: 1;
      
      .section-title {
        font-size: 64px;
        margin-bottom: 20px;
        width: 80%;

        @include medium {
          font-size: 48px;
        }

        @include mobile {
          font-size: 28px;
        }
      }

      .section-socials {
        margin-top: 0;
        
        i {
          color: $pink;
          padding: 0 10px;
          transition: all .5s ease;

          &:hover {
            color: $lightBlue;
          }
        }

      }
    }

    .right-side {
      display: flex;
      flex: 1;
      flex-direction: column;
      justify-content: center;

      form {

        @include mobile {
          margin-top: 10px;
        }

        input {
          border: none;
          border-radius: 5px;
          color: #222;
          font-size: 14px;
          height: 50px;
          margin: 10px 0px;
          padding-left: 10px;
          width: 50%;

          @include mobile {
            margin-right: 10px;
            width: 40%;
          }

          &:focus {
            outline: none;
          }

          &[name="user_subject"] {
            @include mobile {
              width: 90%;
            }
          }
        }

        textarea {
          border: none;
          border-radius: 5px;
          color: #222;
          font-family: 'Roboto', sans-serif;
          font-size: 14px;
          margin: 10px 0px;
          padding: 10px 0 0 10px;
          width: 90%;

          &:focus {
            outline: none;
          }
        }

        button {
          background-color: $pink;
          border: none;
          border-radius: 3px;
          color: #FFF;
          cursor: pointer;
          margin: 0 5px;
          padding: 15px 20px;
          text-decoration: none;
          text-align: center;
          text-transform: uppercase;
          transition: all 0.6s ease;

          &:hover {
            background-color: $lightBlue;
          }
        }
      }
    }
  }
}